import { IGeneralSetting } from './../../../interfaces/i.general.setting.interface';
import { HttpService } from '../../../services/http/http.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FOPModel } from 'src/models/fop/fop.model';
import { FormGroup } from '@angular/forms';
import { SharedComponents } from 'src/app/shared.components';
import { SEOService } from 'src/services/seo.service';
import { AlertService } from 'src/services/alert.service';
import { GeneralSettingService } from 'src/services/general.setting.service';
import { localizedString } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-setting',
  templateUrl: './general.setting.component.html',
  styles: [
  ]
})
/**
 * This a general controller 
 * 
 */
export class GeneralSettingComponent extends SharedComponents implements OnInit {
  /**
   * Page Target
   */
  skip: number = 0;
  limit: number = 50;
  entries: IGeneralSetting[];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  constructor(public u: CurrentUserService, private activRouter: ActivatedRoute, private seo: SEOService, private http: HttpService, public gsService: GeneralSettingService) { super() }

  ngOnInit(): void {
    //Listener On Parems
    this.allSubscriptions.push(
      this.activRouter.params.subscribe(parms => {
        this.gsService.currentPageTargetName = parms.pageTargetName;
        this.seo.updateSEO(this.gsService.currentPageTargetName);
        this.entries = [];
        this.skip = 0
        this.fop = new FOPModel();
        this.getEntiries();
      })

    );
    //Listener On Create New Item Event
    this.allSubscriptions.push(this.gsService.onCreatedNewItem.subscribe(newItem => {
      this.entries = [newItem, ...this.entries];
      this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
    }));
    //Listener On Edit Item Event
    this.allSubscriptions.push(
      this.gsService.onEditedItem.subscribe(editedItem => {
        let editedNameTarget = this.entries.find(ent => ent._id == editedItem._id);
        if (editedNameTarget)
          editedNameTarget.name = editedItem.name;
      }));

    //Listener On Delete Item Event
    this.allSubscriptions.push(
      this.gsService.onDeletedItem.subscribe(deletedItemId => {
        let deletedIndex = this.entries.findIndex(ent => ent._id == deletedItemId);
        if (deletedIndex >= 0) {
          this.entries.splice(deletedIndex, 1);
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
        }
      }));

  }


  /**
 * Get My Users
 */
  getEntiries(isFilter: boolean = false): void {
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.http.getGeneralSettings<IGeneralSetting[]>(this.skip, this.limit, this.gsService.currentPageTargetName).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: IGeneralSetting): string | undefined {
    return entitiy?._id;
  }
}//End Class
