import { Component, OnInit } from '@angular/core';
import { SharedComponents } from 'src/app/shared.components';
import { FOPModel } from 'src/models/fop/fop.model';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { SEOService } from 'src/services/seo.service';
import { HttpService } from 'src/services/http/http.service';
import { AlertService } from 'src/services/alert.service';
import { IState } from 'src/interfaces/i.state.interface';
import { StateService } from 'src/services/state.service';

@Component({
  selector: 'app-state',
  templateUrl: './states.component.html',
  styles: [
  ]
})
export class StatesComponent extends SharedComponents implements OnInit {
  /**
   * Page Target
   */
  skip: number = 0;
  limit: number = 50;
  entries: IState[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  constructor(public u: CurrentUserService, private seo: SEOService, public stateService: StateService, private http: HttpService) { super() }

  ngOnInit(): void {
    this.seo.updateSEO("states");
    this.getEntiries();

    //Listener On Delete Item Event
    this.allSubscriptions.push(
      this.stateService.onDeletedItem.subscribe(deletedItemId => {
        let deletedIndex = this.entries.findIndex(ent => ent._id == deletedItemId);
        if (deletedIndex >= 0) {
          this.entries.splice(deletedIndex, 1);
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
        }
      }));
  }


  /**
 * Get My Users
 */
  getEntiries(): void {
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.http.getStates<IState[]>(this.skip, this.limit).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: IState): string | undefined {
    return entitiy?._id;
  }
}//End CLass
