import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreateGeneralSettingComponent } from "src/app/settings/general-setting/create-general-setting/create.general.setting.component";
import { DeleteGeneralSettingComponent } from "src/app/settings/general-setting/delete-general-setting/delete.general.setting.component";
import { EditGeneralSettingComponent } from "src/app/settings/general-setting/edit-general-setting/edit.general.setting.component";
import { GeneralSettingComponent } from "src/app/settings/general-setting/general.setting.component";
import { CreateStateComponent } from "src/app/settings/states/create-state/create.state.component";
import { DeleteStateComponent } from "src/app/settings/states/delete-state/delete.state.component";
import { EditStateComponent } from "src/app/settings/states/edit-state/edit-state.component";
import { StatesComponent } from "src/app/settings/states/states.component";

const ROUTS: Routes = [
    {
        path: 'states', component: StatesComponent,
        children: [
            {
                path: "delete/:targetId",
                component: DeleteStateComponent,
            }
        ]
    },
    { path: 'states/create', component: CreateStateComponent },
    { path: 'states/edit/:targetId', component: EditStateComponent  },

    //General Setting
    {
        path: ':pageTargetName', component: GeneralSettingComponent,
        children: [
            {
                path: "create",
                component: CreateGeneralSettingComponent
            },
            {
                path: "edit/:targetId",
                component: EditGeneralSettingComponent,
            },
            {
                path: "delete/:targetId",
                component: DeleteGeneralSettingComponent,
            }
        ]
    },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class SettingsRoutingModule {

}

