import { Router } from '@angular/router';
import { Injectable, EventEmitter } from '@angular/core';
import { IGeneralSetting } from 'src/interfaces/i.general.setting.interface';
@Injectable()
export class GeneralSettingService {
    /** 
     * Current Page Target
     */
currentPageTargetName:string;

/** Created New Item Event*/
onCreatedNewItem:EventEmitter<IGeneralSetting>=new EventEmitter<IGeneralSetting>();
/** Updated Item Event*/
onEditedItem:EventEmitter<IGeneralSetting>=new EventEmitter<IGeneralSetting>();
/** Deleted Item Event*/
onDeletedItem:EventEmitter<string>=new EventEmitter<string>();

constructor(private router:Router){}
  /**
   * Close: Back To The Page
   */
 public closeModel(){
 
    this.router.navigate(['/setting',this.currentPageTargetName]);
      }
}//End Class