import { AlertService } from '../../../../services/alert.service';
import { CurrentUserService } from '../../../../services/authentcation/current.user.service';
import { IGeneralSetting } from '../../../../interfaces/i.general.setting.interface';
import { HttpService } from '../../../../services/http/http.service';
import { Router } from '@angular/router';
import { GeneralSettingService } from '../../../../services/general.setting.service';
import { Component, Input, IterableDiffers, OnInit } from '@angular/core';
import { ActivatedRoute, ActivationEnd } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SharedComponents } from 'src/app/shared.components';

@Component({
  selector: 'app-delete-general-setting',
  templateUrl: './delete.general.setting.component.html',
  styles: [
  ]
})
export class DeleteGeneralSettingComponent extends SharedComponents implements OnInit {
  form: FormGroup = new FormGroup({
    _id: new FormControl(null, Validators.required),
    name: new FormControl(null, Validators.required)
  });
  formSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  constructor(public u: CurrentUserService, public gsService: GeneralSettingService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    if (!this.activateRouter.snapshot.params.targetId) {
      this.gsService.closeModel();
      return;
    }

    this.form.controls._id.setValue(this.activateRouter.snapshot.params.targetId);
    this.form.disable();

    //Check If Must Be Get Object From The Server
    if (history.state.deletingItemName) {
      this.form.controls.name.setValue(history.state.deletingItemName);
      history.state.deletingItemName = null;
    }
    else {
      //Get From The Serer
      this.getGenralSettingToUpdate();
    }
  }

  /**
   * Get Genral Setting From Server To Update
   */
  getGenralSettingToUpdate() {
    this.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.getGenralSettingToUpdate<IGeneralSetting>(this.activateRouter.snapshot.params.targetId, this.gsService.currentPageTargetName)
        .subscribe(res => {
          this.isRequestingToServer = false;
          if (res.isSuccess) {
            this.form.controls.name.setValue(res.result.name);
          }
        }, error => {
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.form.invalid) {
      this.formSubmitError = true;
      return;
    }
    this.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.deleteGeneralSetting<IGeneralSetting>(this.form.value, this.gsService.currentPageTargetName)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.gsService.onDeletedItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.gsService.closeModel();
          }
        }, error => {
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

}//End Class
