import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { IState } from 'src/interfaces/i.state.interface';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { StateService } from 'src/services/state.service';

@Component({
  selector: 'app-delete-state',
  templateUrl: './delete.state.component.html',
  styles: [
  ]
})
export class DeleteStateComponent extends SharedComponents implements OnInit {

  form: FormGroup = new FormGroup({
    _id: new FormControl(null, Validators.required),
    name: new FormControl(null, Validators.required)
  });
  formSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  constructor(public u: CurrentUserService, public stateService: StateService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.form.controls._id.setValue(this.activateRouter.snapshot.params.targetId);
    this.form.disable();

    //Check If Must Be Get Object From The Server
    if (history.state.deletingItemName) {
      this.form.controls.name.setValue(history.state.deletingItemName);
      history.state.deletingItemName = null;
    }
    else {
      //Get From The Serer
      this.getStateToUpdate();
    }
  }

  /**
   * Get Car Mark From Server To Update
   */
  getStateToUpdate() {
    this.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.getStateToUpdate<IState>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.isRequestingToServer = false;
          if (res.isSuccess) {
            this.form.controls.name.setValue(res.result.name);
          }
        }, error => {
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.form.invalid) {
      this.formSubmitError = true;
      return;
    }
    this.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.http.deleteState<IState>(this.form.value)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.stateService.onDeletedItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.stateService.closeModel();
          }
        }, error => {
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
