import { HttpService } from 'src/services/http/http.service';
import { SweetAlertService } from 'src/services/sweet.alert.service';
import { StateService } from 'src/services/state.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { SharedComponents } from 'src/app/shared.components';
import { SEOService } from './../../../../services/seo.service';
import { IState } from '../../../../interfaces/i.state.interface';
import { FormArray, FormBuilder, FormControl, FormControlName, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from 'src/services/alert.service';
import { CustomValidatorsService } from 'src/services/custom.validators.service';

@Component({
  selector: 'app-create-state',
  templateUrl: './create.state.component.html',
  styles: [
  ]
})
export class CreateStateComponent extends SharedComponents implements OnInit {
  titleResourcesKey = "createState";
  cites: [];
  constructor(
    public u: CurrentUserService, private seo: SEOService, private http: HttpService,
    public stateService: StateService,
    private router: Router,
    public sweetAlertService: SweetAlertService) { super() }

  ngOnInit(): void {
    this.seo.updateSEO(this.titleResourcesKey);
    this.stateService.initForm();
    this.stateService.formCities.reset();
  }


  /**
    * Create New Item
    */
  onSubmit() {
    if (this.stateService.form.invalid) {
      this.stateService.formSubmitError = true;
      return;
    }
    this.stateService.isRequestingToServer = true;
    this.stateService.formCiteisSubmitError = false;
    this.stateService.formSubmitError = false;

    this.stateService.form.disable();

    let objSaving = { ...this.stateService.form.value, ...{ cities: this.stateService.formCities.getRawValue().listOfCities } };

    this.allSubscriptions.push(
      this.http.createNewState<IState>(objSaving)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          if (res.isSuccess) {
            this.stateService.form.reset();
            this.stateService.formCities.reset();

            this.stateService.isRequestingToServer = false;

            this.stateService.form.enable();
            this.sweetAlertService.confirm(this.u.r.doYouWantEditCreatedItem, this.u.r.goToEdit, this.u.r.createNew).then(() => {
              this.router.navigate(['/setting', 'states', 'edit', res.result._id])
            })
          }
        }, error => {
          this.stateService.form.enable();
          this.stateService.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

}//End Class
