import { IGeneralSetting } from './../../../../interfaces/i.general.setting.interface';
import { HttpService } from './../../../../services/http/http.service';
import { Router } from '@angular/router';
import { GeneralSettingService } from './../../../../services/general.setting.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { Component, Input, IterableDiffers, OnInit } from '@angular/core';
import { ActivatedRoute, ActivationEnd } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';

@Component({
  selector: 'app-create-general-setting',
  templateUrl: './create.general.setting.component.html',
  styles: [
  ]
})
export class CreateGeneralSettingComponent extends SharedComponents implements OnInit {
  form: FormGroup = new FormGroup({
    name: new FormControl(null, Validators.required)
  });
  formSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  constructor(public u: CurrentUserService, public gsService: GeneralSettingService, public http: HttpService) { super() }

  ngOnInit(): void {
    
  }

  /**
   * Create New Item
   */
  onSubmit() {
    if (this.form.invalid) {
      this.formSubmitError = true;
      return;
    }
    this.isRequestingToServer = true;
    this.form.disable();
    this.allSubscriptions.push(
      this.http.createNewGeneralSetting<IGeneralSetting>(this.form.value, this.gsService.currentPageTargetName)
        .subscribe(res => {

          AlertService.showResponseMessage(res);
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.gsService.onCreatedNewItem.emit(res.result);
            //Back To Component
            this.gsService.closeModel();
          }
        }, error => {
          this.form.enable();
          this.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

}//End Class
